import React from "react";
import Announcement from "../components/Announcement";
import NewsLetter from "../components/NewsLetter";
import Footer from "../components/Footer";
import styled from "styled-components";
import Navbar from "../components/Navbar";
import { mobile, tablet } from "../responsive";

const Container = styled.div`
  width: 100%;
`;
const Wrapper = styled.div`
  width: 100%;
  object-fit: cover;
  display: flex;

  ${mobile({ flexDirection: "column" })}
  ${tablet({ flexDirection: "column" })}
`;
const ImgContainer = styled.div`
  flex: 1;
  padding: 15px;
`;
const Image = styled.img`
  width: 100%;
`;
const ScriptProduct = styled.div`
  flex: 1;
  padding: 15px;
`;
const TitlePro = styled.h1`
  font-weight: 300;
  font-size: 50px;
`;
const Desc = styled.div``;
export const Price = styled.div`
  font-size: 50px;
  font-weight: 300;

  margin: 5% auto;
`;
export const Currency = styled.span``;
const FilterContainer = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;

  align-items: center;
  margin: auto;
`;
const ColorFilter = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;
export const Color = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: ${(props) => props.color};
  cursor: pointer;
  margin: 0 5px;
`;
const Title = styled.div`
  font-size: 20px;
`;
const SizeFilter = styled.select`
  margin: 0 5px;
  padding: 10px;
`;
const Option = styled.option``;
export const ControlContainer = styled.div`
  flex: 1;
  display: flex;
  font-size: 30px;
  font-weight: 400;
`;
export const Button = styled.button`
  flex: 1;
  cursor: pointer;
  padding: 10px 10px;
  background-color: pink;
  border-radius: 5%;
  border: 2px pink solid;
  color: white;
  margin-top: 5%;
  font-size: 20px;
`;
export const Control = styled.div`
  cursor: pointer;
`;
export const AmountNumber = styled.div`
  margin: 0 10px;
  border: 1px solid pink;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Product = () => {
  return (
    <div>
      <Container>
        <Navbar />
        <Announcement />
        <Wrapper>
          <ImgContainer>
            <Image src='../pexels-christian-1164778.jpg' />
          </ImgContainer>
          <ScriptProduct>
            <TitlePro>Mercedec</TitlePro>
            <Desc>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Eius
              aliquam inventore odio accusamus placeat fuga facilis. Eveniet
              quasi necessitatibus quos ipsum commodi vitae beatae
              exercitationem nulla ullam, earum iusto eaque, officia iure
              ratione laborum enim? Praesentium suscipit dolore distinctio,
              corrupti aliquam velit possimus eius omnis deleniti qui, ullam
              sapiente!
            </Desc>
            <Price>
              <Currency>$ </Currency>20
            </Price>
            <FilterContainer>
              <ColorFilter>
                <Title> Color:</Title>
                <Color color='blue'></Color>
                <Color color='black'></Color>
                <Color color='gray'></Color>
              </ColorFilter>
              <Title>Size </Title>
              <SizeFilter>
                <Option disabled selected>
                  Xs
                </Option>
                <Option>Xs</Option>
                <Option>S</Option>
                <Option>M</Option>
                <Option>L</Option>
                <Option>XL</Option>
              </SizeFilter>
            </FilterContainer>
            <FilterContainer>
              <ControlContainer>
                <Control>-</Control>
                <AmountNumber>1</AmountNumber>
                <Control>+</Control>
              </ControlContainer>
              <Button>ADD TO CART</Button>
            </FilterContainer>
          </ScriptProduct>
        </Wrapper>
        <NewsLetter />
        <Footer />
      </Container>
    </div>
  );
};

export default Product;
