import React from "react";
import styled from "styled-components";
import Navbar from "../components/Navbar";
import Announcement from "../components/Announcement";
import NewsLetter from "../components/NewsLetter";
import Footer from "../components/Footer";
import {
  Color,
  Currency,
  Price,
  ControlContainer,
  AmountNumber,
  Control,
} from "./Product";

const Container = styled.div`
  width: 100%;
`;
const Wrapper = styled.div`
  width: 90%;
  margin: auto;
`;
const Title = styled.h1`
  font-size: 20px;
  text-align: center;
`;
const TopRow = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  align-items: center;
`;
const MainRow = styled.div`
  display: flex;

  @media (max-width: 1020px) {
    flex-direction: column;
  }

  margin: 10px 0;
`;
const Button = styled.button`
  padding: 10px;
  border-size: 1px;
  border-color: #2fe4eb;
  margin: 5px;
  border-style: solid;
  border: ${(props) => props.filled && "none"};
  border-radius: 10px;
  background-color: ${(props) => (props.filled ? "black" : "transparent")};
  color: ${(props) => (props.filled ? " #2fe4eb" : "black")};
  cursor: pointer;
`;
const BagContainer = styled.div``;
const UnderLine = styled.span`
  margin: 0 10px;
  cursor: pointer;
  text-decoration: underline;
`;
const Product = styled.div`
  display: flex;
  margin: 10px 0;
  @media (max-width: 630px) {
    flex-direction: column;
  }
`;
const ProductImg = styled.div`
  margin-right: 10px;

  flex: 1;
`;
const ProductProfile = styled.div`
  flex: 3;
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
  @media (max-width: 1020px) {
    padding-right: 0px;
  }
`;
const ProductInfo = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
`;
const ProductTitle = styled.div``;
const Image = styled.img`
  height: 150px;
  border-radius: 10px;
  width: 100%;
  object-fit: cover;
`;
const ProductId = styled.div``;
const ProductSize = styled.div``;
const ProductColor = styled.div``;
const ContainerControl = styled.div``;
const PriceCon = styled.div``;
const Products = styled.div`
  flex: 2;
`;
const Br = styled.hr`
  border: 1px solid #e4f7f7;
`;
const Summary = styled.div`
  border: 2px solid #e4f7f7;
  height: 50vh;
  border-radius: 10px;
  flex: 1;
  @media (max-width: 850px) {
    padding-bottom: 15px;
  }
  padding: 0 15px;
`;
const SummaryTitle = styled.h2`
  text-align: center;
  font-size: 30px;
  font-weight: 300;
`;
const SummaryElement = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
const SumContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
`;
const SubTitle = styled.div`
  font-size: 20px;
`;
const PriceSum = styled.div`
  font-size: 25px;
  font-weight: 400;
`;

const ShoppingCart = () => {
  return (
    <Container>
      <Navbar />
      <Announcement />
      <Wrapper>
        <Title>YOUR BAG</Title>
        <TopRow>
          <Button>Continue Shopping</Button>
          <BagContainer>
            <UnderLine>Shopping Bag(1)</UnderLine>
            <UnderLine>Your Wishlist(0)</UnderLine>
          </BagContainer>
          <Button filled>CheckOut Now</Button>
        </TopRow>
        <MainRow>
          <Products>
            <Product>
              <ProductImg>
                <Image src='../pexels-yurii-hlei-1545743.jpg' />
              </ProductImg>
              <ProductProfile>
                <ProductInfo>
                  <ProductTitle>
                    <b>Product : </b>Lamborgeny
                  </ProductTitle>
                  <ProductId>
                    <b>ID : </b>23534643
                  </ProductId>
                  <Color color='black'></Color>
                  <ProductSize>
                    <b>Size : </b>40
                  </ProductSize>
                </ProductInfo>
                <ContainerControl>
                  <ControlContainer>
                    <Control>-</Control>
                    <AmountNumber>2</AmountNumber>
                    <Control>+</Control>
                  </ControlContainer>
                  <Price>
                    20
                    <Currency>$</Currency>
                  </Price>
                </ContainerControl>
              </ProductProfile>
            </Product>
            <Br />
            <Product>
              <ProductImg>
                <Image src='../pexels-yurii-hlei-1545743.jpg' />
              </ProductImg>
              <ProductProfile>
                <ProductInfo>
                  <ProductTitle>
                    <b>Product : </b>Lamborgeny
                  </ProductTitle>
                  <ProductId>
                    <b>ID : </b>23534643
                  </ProductId>
                  <Color color='black'></Color>
                  <ProductSize>
                    <b>Size : </b>40
                  </ProductSize>
                </ProductInfo>
                <ContainerControl>
                  <ControlContainer>
                    <Control>-</Control>
                    <AmountNumber>2</AmountNumber>
                    <Control>+</Control>
                  </ControlContainer>
                  <Price>
                    20
                    <Currency>$</Currency>
                  </Price>
                </ContainerControl>
              </ProductProfile>
            </Product>
            <Br />
            <Product>
              <ProductImg>
                <Image src='../pexels-yurii-hlei-1545743.jpg' />
              </ProductImg>
              <ProductProfile>
                <ProductInfo>
                  <ProductTitle>
                    <b>Product : </b>Lamborgeny
                  </ProductTitle>
                  <ProductId>
                    <b>ID : </b>23534643
                  </ProductId>
                  <Color color='black'></Color>
                  <ProductSize>
                    <b>Size : </b>40
                  </ProductSize>
                </ProductInfo>
                <ContainerControl>
                  <ControlContainer>
                    <Control>-</Control>
                    <AmountNumber>2</AmountNumber>
                    <Control>+</Control>
                  </ControlContainer>
                  <Price>
                    20
                    <Currency>$</Currency>
                  </Price>
                </ContainerControl>
              </ProductProfile>
            </Product>
            <Br />
          </Products>
          <Summary>
            <SummaryTitle>ORDER SUMMERY</SummaryTitle>
            <SumContainer>
              <SummaryElement>
                <SubTitle>SubTotal</SubTitle>
                <PriceSum>
                  11<Currency>$</Currency>
                </PriceSum>
              </SummaryElement>
              <SummaryElement>
                <SubTitle>Estimated Shipping</SubTitle>
                <PriceSum>
                  9<Currency>$</Currency>
                </PriceSum>
              </SummaryElement>
              <SummaryElement>
                <SubTitle>Shipping Discount</SubTitle>
                <PriceSum>
                  10<Currency>$</Currency>
                </PriceSum>
              </SummaryElement>

              <SummaryElement style={{ margin: 5, color: "red" }}>
                <SubTitle>
                  <b> Total</b>
                </SubTitle>
                <PriceSum>
                  20<Currency>$</Currency>
                </PriceSum>
              </SummaryElement>
              <Button filled>CHECKOUT NOW</Button>
            </SumContainer>
          </Summary>
        </MainRow>
      </Wrapper>
      <NewsLetter />
      <Footer />
    </Container>
  );
};

export default ShoppingCart;
